<!-- eslint-disable vue/no-multiple-template-root -->
<!-- eslint-disable no-return-assign -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable max-len -->
<template>
  <div v-if="cartLoading">Загрузка корзины</div>
  <div v-else-if="cartLoadingFailed">Ошибка загрузки корзины</div>
    <main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{name:'main'}" class="breadcrumbs__link">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info" >
      {{ totalAmount | pluralize}}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
           <CartItem v-for="item in products" :key="item.productId" :item="item"/>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы посчитаем стоимость доставки на следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{totalPrice | numberFormat}} ₽</span>
          </p>

          <router-link tag="button" :to="{name: 'order'}" class="cart__button button button--primery" type="submit"
          v-show="totalAmount > 0">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters, mapActions } from 'vuex';
import pluralize from '@/helpers/pluralize';
import CartItem from '@/components/CartItem.vue';

export default {
  name: 'CartPage',
  data() {
    return {
      cartLoadingFailed: false,
      cartLoading: false,
    };
  },
  filters: {
    numberFormat,
    pluralize,
  },
  components: { CartItem },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', totalAmount: 'cartTotalAmount' }),
  },
  methods: {
    ...mapActions(['loadCart']),

    loadingCart() {
      this.cartLoadingFailed = false;
      this.cartLoading = true;
      this.loadCart()
        // eslint-disable-next-line no-return-assign
        .catch(() => this.cartLoadingFailed = true)
        // eslint-disable-next-line no-return-assign
        .then(() => this.cartLoading = false);
    },
  },
  created() {
    this.loadingCart();
  },
};
</script>
